.item{
    border-radius: 2px;
    width: 100%;
    border-width: 1px;
    border-style: solid;
    border-color: #cecece;
    cursor: pointer;
}

.item:hover{
    /* border-width: 2px; */
    border-color: rgb(34, 120, 230);
}

.item:not(:last-child){
    margin-bottom: 12px;
}

.item-photo-container{
    margin-right: 8px;
}

.item-photo{
    width: 242px;
    height: 162px;
    border-radius: 2px;
    overflow: hidden;
    margin-bottom: 6px;
}

img{
    display: block;
    height: 100%;
    width: 100%;
}

.item-thumbnail{
    height: 48px;
    width: 56px;
}

.item-thumbnail:not(:last-child){
    margin-right: 6px;
}

.chip-success {
    background-color: green !important;
    color: #fff !important;
}

.chip-danger {
    background-color: red;
    color: #fff;
}